import React, {useState, useEffect} from 'react'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Lock from '@material-ui/icons/Lock'
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import { User } from '../model/user.model'
import './personal.scss'

export default function Personal() {
  const [firstName, setFirstName] = useState<string>('')
  const [lastName, setLastName] = useState<string>('')
  const [street, setStreet] = useState<string>('')
  const [postal, setPostal] = useState<string>('')
  const [city, setCity] = useState<string>('')
  const [phone, setPhone] = useState<string>('')

  useEffect(() => {
    const userInformation = localStorage.getItem('userInformation')
    const user: User = JSON.parse(userInformation || "{}")

    setFirstName(user?.firstName)
    setLastName(user?.lastName)
    setStreet(user?.street)
    setPostal(user?.postal)
    setCity(user?.city)
    setPhone(user?.phone)
  }, [])

  const saveUser = () => {
    localStorage.setItem(
      'userInformation',
      JSON.stringify({ firstName, lastName, street, postal, city, phone })  || '{}')
  }

  return (
    <main>
      <Card className="card">
        <form>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField value={firstName} onInput={(e) => setFirstName((e.target as HTMLInputElement).value)} label="Vorname" />
            </Grid>
            <Grid item xs={6}>
              <TextField value={lastName} onInput={(e) => setLastName((e.target as HTMLInputElement).value)} label="Nachname" />
            </Grid>
            <Grid item xs={12}>
              <TextField value={street} onInput={(e) => setStreet((e.target as HTMLInputElement).value)} label="Straße" fullWidth />
            </Grid>
            <Grid item xs={4}>
              <TextField value={postal} onInput={(e) => setPostal((e.target as HTMLInputElement).value)} label="Plz" />
            </Grid>
            <Grid item xs={8}>
              <TextField value={city} onInput={(e) => setCity((e.target as HTMLInputElement).value)} label="Stadt" fullWidth />
            </Grid>
            <Grid item xs={12}>
              <TextField value={phone} onInput={(e) => setPhone((e.target as HTMLInputElement).value)} label="Telefonnummer" fullWidth />
            </Grid>
          </Grid>
          <br />
          <CardActions>
            <Button onClick={() => saveUser()}
                    variant="contained"
                    color="primary"
                    fullWidth={true}
                    endIcon={<Lock />}>
              Speichern
            </Button>
          </CardActions>
        </form>
      </Card>
    </main>
  )
}