import React, { useState, useEffect } from 'react'
import Button from '@material-ui/core/Button';
import PhotoCamera from '@material-ui/icons/PhotoCamera'
import { defineCustomElements } from '@ionic/pwa-elements/loader'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import MuiAlert from '@material-ui/lab/Alert';
// @ts-ignore
import QRCode from 'qrcode.react'
// @ts-ignore
import QrReader from 'react-qr-reader'
import { User } from '../model/user.model'
import "./homescreen.scss"

export default function Homescreen() {
  const [user, setUser] = useState<User | null>(null)
  const [personalQrCode, setPersonalQrCode] = useState<string | null>(null)
  const [readerActive, setReaderActive] = useState<boolean>(false)

  useEffect(() => {
    defineCustomElements(window)
    const userInformation = localStorage.getItem("userInformation")
    setPersonalQrCode(userInformation)
  }, [])

  const readQrCode = (data: string) => {
    if (data) {
      setUser(JSON.parse(data) as User)
      setReaderActive(false)
    }
  }

  return (
    <main>
      <Card className="card">
        <CardContent className="card-content">
          {readerActive &&
            <QrReader
              onScan={readQrCode}
              style={{ width: '250px' }}
            />
          }
          {(personalQrCode && !readerActive) &&
            <QRCode value={personalQrCode}
                    size={250} />
          }
        </CardContent>
        <div className="card-text">Dein persönlicher QR-Code</div>
        <CardActions>
          <Button onClick={() => setReaderActive(!readerActive)}
            variant="contained"
            color="primary"
            fullWidth={true}
            endIcon={<PhotoCamera />}>
            {!readerActive ? 'QR-Code Scannen' : 'Scannen abbrechen'}
            </Button>
        </CardActions>
      </Card>
      {user &&
        <MuiAlert onClick={() => setUser(null)}
                  elevation={6}
                  variant="filled"
                  severity="success">
          {`Scanned: ${user.firstName} ${user.lastName}`}
        </MuiAlert>
      }
    </main>
  )
}